const regExp = /^[A-z-]*$/;
export default function loginPageValidator(str: string, strMinLength: number) {
    const error: string[] = [];
    if (!str) {
        error.push('–ü–æ–ª–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º');
    } else {
        if (str.length < strMinLength) {
            error.push(`–º–Ω–∏–º–∞–ª—å–Ω–∞—è –¥–ª–∏–Ω–∞ = ${strMinLength}`);
        }
        if (str[0]!.toUpperCase() !== str[0]) {
            error.push('–ø–µ—Ä–≤–∞—è –±—É–∫–≤–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –∑–∞–≥–ª–∞–≤–Ω–æ–π');
        }
        if (!regExp.test(str)) {
            error.push('—Ç–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω–∏—Ü–∞ –∏ "-"');
        }
    }
    return error.join(', ');
}
